.keyboard {
    position: fixed;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 9999;
    font-size: .48rem;
    line-height: .48rem;
    background: #f6f6f6;
    color: #4a4a4a;
    width: 100%;
    height: 4.4rem;
    user-select: none;
    visibility: visible;
    transform: translate3d(0, 0, 0);
    transition-property: transform visibility;
    transition-duration: .2s;
    font-weight: normal;
    border-top: 1px solid #e5e5e5;
}
.keyboard.hidden {
    visibility: hidden;
    transform: translate3d(0, 100%, 0);
}
.keyboard table {
    width: 100%;
    height: 100%;
    border-collapse: collapse;
    table-layout: fixed;
}
.keyboard td {
    text-align: center;
    width: 25%;
    border: 1px solid #ddd;
}
.keyboard td:nth-of-type(-n+3) {
    font-weight: normal;
}
.keyboard td:active {
    background-color: #ddd;
}
.keyboard td:first-child {
    border-left: none;
}
.keyboard td:last-child {
    border-right: none;
}
.keyboard td.active {
    background: #dff0ff;
}
.keyboard td.transition {
    transition: background ease .1s;
}
.keyboard .icon {
    font-size: .34rem;
    background-size: contain;
    background-position: center;
    background-repeat: no-repeat;
    width: 1.5em;
    height: 1.5em;
    display: inline-block;
}
.keyboard .icon svg {
    width: 1.5em;
    height: 1.5em;
}
.keyboard .icon.del {
    background-image: url(data:image/svg+xml;base64,);
}
.keyboard .icon.clear {
    background-image: url(data:image/svg+xml;base64,);
}
.keyboard .icon.keyfold {
    background-image: url(data:image/svg+xml;base64,);
}
.keyboard td[data-code='K'] {
    font-size: .34rem;
    background: #2b91e2;
    color: #ddd;
}
.keyboard td[data-code='K'].active {
    color: #fff;
}
@keyframes cursor {
    0% {
        opacity: 1;
    }
    50% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}
.keyboard-input {
    display: inline-block;
    text-align: right;
    width: 100%;
    height: 100%;
}
.keyboard-input.keyboard-focus {
    position: relative;
}
.keyboard-input.keyboard-focus::after {
    content: '';
    position: absolute;
    right: 0;
    top: 10%;
    height: 80%;
    border-right: 0.02rem solid #108EE9;
    animation: cursor infinite 1s step-start;
}
.keyboard-input span {
    display: inline-block;
}
.keyboard-input .placeholder {
    color: #ddd;
}

/**
   * css for text-left
   */
.keyboard-input.left {
    text-align: left;
}
.keyboard-input.left .icon-clear {
    position: absolute;
    right: 0;
    top: 50%;
    transform: translateY(-50%);
