header {
    margin: 0 auto;
    font-size: 14px;
    display: flex;
    position: relative;
    z-index: 9;
}
header #leftNav {
    height: 100%;
    box-sizing: border-box;
}
header #leftNav .logo {
    display: block;
    height: 40px;
    width: 170px;
    background-image: url(data:image/png;base64,);
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center;
    text-indent: -9999px;
}
header #rightNav {
    display: block;
}
header #rightNav .infoMem {
    width: auto;
    text-align: right;
    font-weight: bold;
    display: flex;
    height: 100%;
}
header #rightNav .infoMem a.router-link-active {
    opacity: 1 !important;
}
header #rightNav .infoMem li {
    position: relative;
    padding: 0px 10px;
    line-height: 44px;
    font-size: 14px;
}
header #rightNav .infoMem .deposit {
    border-radius: 35px;
}
header #rightNav .infoMem .deposit .labelIcon {
    left: 17px;
    top: 1px;
}
header #rightNav .infoMem .deposit a {
    color: #fff;
}
header #rightNav .infoMem .deposit button {
    transition: transform .3s ease;
    background-color: #00B771;
    padding: 0 16px 0 40px;
    line-height: 44px;
    height: 44px;
}
header #rightNav .infoMem .deposit button:hover {
    transform: translateY(-3px);
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.4);
}
header #rightNav .infoMem .deposit button:focus, header #rightNav .infoMem .deposit button:active {
    transform: translateY(0);
}
header #rightNav .infoMem .transfer .labelIcon {
    left: 12px;
    top: 1px;
}
header #rightNav .infoMem .transfer .labelIcon svg {
    width: 20px;
}
header #rightNav .infoMem .transfer a {
    color: #889196;
}
header #rightNav .infoMem .transfer a:hover {
    text-decoration: none;
    color: #fff;
}
header #rightNav .infoMem .transfer button {
    padding: 0 15px 0 40px;
    line-height: 38px;
    height: 44px;
}
header #rightNav .infoMem .mybet .icon {
    width: 25px;
    display: inline-block;
    height: 25px;
    position: relative;
    top: 10px;
}
header #rightNav .infoMem .mybet a {
    color: #fff;
    opacity: .3;
}
header #rightNav .infoMem .mybet a:hover {
    text-decoration: none;
    opacity: 1;
}
header #rightNav .infoMem .balance a:hover {
    text-decoration: none;
}
header #rightNav .infoMem .balance .currency {
    font-weight: bold;
    color: #fff;
    position: relative;
    margin-right: 5px;
    padding-right: 0 !important;
}
header #rightNav .infoMem .balance .currency .unit {
    color: #fff;
    padding: 3px;
    background: #F0972D;
    border-radius: 50%;
    width: 30px;
    height: 30px;
    display: block;
    line-height: 28px;
    position: absolute;
    top: -8px;
    text-align: center;
    right: 0px;
    font-size: 14px;
}
header #rightNav .infoMem .balance .mainCoin {
    width: 22px;
    height: 22px;
}
header #rightNav .infoMem .inbox a {
    position: relative;
    display: block;
}
header #rightNav .infoMem .inbox a img {
    width: 35px;
    height: auto;
    vertical-align: middle;
}
header #rightNav .infoMem .inbox a span.number {
    position: absolute;
    top: 5px;
    right: 0;
    display: block;
    overflow: hidden;
    width: 14px;
    height: 14px;
    background-color: #ef3b3b;
    border-radius: 50%;
    color: #fff;
    line-height: 12px;
    font-size: 10px;
    font-weight: 100;
    text-align: center;
}
header #rightNav .infoMem .profile {
    position: relative;
}
header #rightNav .infoMem .profile .detailProfile {
    position: relative;
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
    display: flex;
    align-items: center;
    vertical-align: middle;
}
header #rightNav .infoMem .profile .detailProfile .name {
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
    display: block;
    float: right;
    max-width: 191px;
}
header #rightNav .infoMem .profile .detailProfile i {
    font-size: 16px;
}
header #rightNav .infoMem .profile .detailProfile .prepare-medium {
    display: none;
}
header #rightNav .infoMem .profile .detailProfile.active .arrow {
    transform: rotateX(180deg);
}
header #rightNav .infoMem .profile .popupInfo {
    position: absolute;
    top: 56px;
    right: 0px;
    left: auto !important;
    width: 220px;
    text-align: left;
    color: #fff;
    background: #12252E;
    font-size: 14px;
    overflow: hidden;
    box-shadow: 0px 1px 5px 4px rgba(0, 0, 0, 0.1);
}
header #rightNav .infoMem .profile .popupInfo .intro {
    padding: 15px 15px 3px;
    color: white;
}
header #rightNav .infoMem .profile .popupInfo .intro .button {
    margin: 10px 0px;
}
header #rightNav .infoMem .profile .popupInfo .intro .button.investor {
    width: 100%;
    padding: 11px 5px;
}
header #rightNav .infoMem .profile .popupInfo .listTools li .labelIcon {
    display: inline-block;
}
header #rightNav .infoMem .profile .popupInfo .listTools li .labelIcon svg {
    width: 20px;
    position: relative;
    top: 5px;
}
header #rightNav .infoMem .profile .popupInfo .listTools li .labelIcon svg .icon-giftcode {
    fill: #fff;
}
header #rightNav .infoMem .profile .popupInfo .listTools li a {
    display: block;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}
header #rightNav .infoMem .profile .popupInfo .listTools li a:hover {
    text-decoration: none;
}
header #rightNav .infoMem .profile .popupInfo .listTools li a.active {
    color: #F0972D;
}
header #rightNav .infoMem .profile .popupInfo .listTools li a.active svg .icon-giftcode {
    fill: #F0972D;
}
header #rightNav .infoMem .profile .popupInfo .listTools li.logout {
    background: rgba(255, 255, 255, 0.1);
}
header #rightNav .infoMem .profile .popupInfo .listTools li:hover {
    background: rgba(255, 255, 255, 0.1);
}
header #rightNav .infoMem .profile .popupInfo.active {
    display: block !important;
}
header #rightNav .infoMem .nickName {
    color: #fff;
}
header #rightNav button {
    line-height: 100%;
    height: 100%;
}
.arrow {
    transition: transform .3s ease;
    display: inline-block;
}
[data-toggle='dropdown'][aria-expanded='true'] .arrow, [data-toggle='collapse'][aria-expanded='true'] .arrow {
    transform: rotateX(180deg);
}
#leftSidebar {
    width: 105px;
    min-width: 105px;
    color: rgba(255, 255, 255, 0.5);
    background-color: #12013E;
    font-size: 16px;
    padding: 0px;
}
#leftSidebar .topSidebar .intro {
    color: white;
    padding: 20px;
}
#leftSidebar .topSidebar .intro .button.investor {
    width: 100%;
}
#leftSidebar .title {
    padding: 0px 25px;
}
#leftSidebar .title.left h4 {
    border: none;
    margin: 0;
    padding: 5px 10px;
    position: relative;
}
#leftSidebar .title.left h4::after {
    content: '';
    width: 3px;
    height: 28px;
    background: #00C67A;
    position: absolute;
    top: 4px;
    left: 0px;
    border-radius: 50px;
}
#leftSidebar .title h4 {
    font-size: 20px;
    color: white;
    border-bottom: 1px solid #868F93;
    padding-bottom: 10px;
}
#leftSidebar .box .listItem #box-list {
    position: relative;
}
#leftSidebar .box .listItem .list li .titleItem {
    margin-top: 20px;
}
#leftSidebar .box .listItem .list li .titleItem.active {
    background-color: #12252E;
    height: 40px;
}
#leftSidebar .box .listItem .list li .titleItem .above {
    display: block;
    position: absolute;
    z-index: 2;
}
#leftSidebar .box .listItem .list li a {
    padding: 0px 25px;
    line-height: 40px;
    height: 40px;
    display: block;
    color: #fff;
    font-size: 16px;
    text-decoration: none;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}
#leftSidebar .box .listItem .list li a:hover {
    background: #12252E;
}
#leftSidebar .box .listItem .list li .child {
    padding-left: 0;
}
#leftSidebar .box .listItem .list li .child .hasChild a.itemChild {
    padding-left: 40px;
    transition: all 300ms ease;
}
#leftSidebar .box .listItem .list li .child .hasChild a.itemChild:hover {
    color: #F0972D;
}
#leftSidebar .box .listItem .list li .child .hasChild a.itemChild[aria-expanded='true'] {
    color: #F0972D;
}
#leftSidebar .box .listItem .list li .child .hasChild a.itemChild[aria-expanded='true']:before {
    transform: rotate(90deg) translate(4px, 4px);
}
#leftSidebar .box .listItem .list li .child .hasChild a.selectItem {
    padding-left: 60px;
}
#leftSidebar .box .listItem .list li .child .hasChild .activeChild {
    color: #F0972D;
}
#leftSidebar .box .listItem .list li .child .hasChild a.arrow::before {
    font-family: 'Font Awesome 5 Free';
    font-weight: 900;
    display: inline-block;
    padding-right: 10px;
    vertical-align: middle;
    content: "\F105";
    position: relative;
    z-index: 2;
}
#leftSidebar .box .listItem .list li .selected {
    color: #F0972D;
}
#leftSidebar .box .listItem .list li.itemParent a {
    margin-top: 0;
}
.sportList .selectItem.active, .sportList .itemChild.active,
.sportList .selectItem.active-url, .sportList .itemChild.active-url {
    color: #F0972D !important;
    opacity: 1 !important;
}
.boxSearch {
    width: 100%;
    color: #868F93;
    margin-bottom: 30px;
    position: relative;
    padding: 5px 20px;
    background: rgba(255, 255, 255, 0.1);
}
.boxSearch .search {
    padding: 5px;
    position: relative;
}
.boxSearch .search #iconSearch {
    position: absolute;
    display: block;
    left: 10px;
    top: 10px;
    width: 20px;
    height: 20px;
    background-size: contain;
    background-image: url(data:image/svg+xml;base64,);
}
.boxSearch .search #iconSearch.active {
    color: white;
}
.boxSearch .search .close {
    position: absolute;
    top: 12px;
    right: 10px;
    width: 15px;
    height: 20px;
    background: url(data:image/svg+xml;base64,);
    background-position: center;
    background-repeat: no-repeat;
}
.boxSearch .search input {
    background: none;
    border: none;
    color: #868F93;
    outline: none;
    font-size: 16px;
    width: 100%;
    padding: 5px 35px;
}
.boxSearch .search input:focus {
    outline: none;
    color: white;
}
.boxSearch .resultSearch {
    position: absolute;
    top: 55px;
    left: 0px;
    width: 100%;
    z-index: 10;
    background: #12252E;
}
.boxSearch .resultSearch .boxResult {
    box-shadow: 0px 1px 3px #1f1c1c;
}
.boxSearch .resultSearch .boxResult .titleResult {
    width: 100%;
    padding: 15px 20px;
    background: #12252E;
    border-radius: 5px;
}
.boxSearch .resultSearch .boxResult .titleResult h5 {
    font-size: 16px;
    color: #fff;
    border-left: 2px solid #F0972D;
    padding-left: 10px;
    font-weight: bold;
}
.boxSearch .resultSearch .boxResult .listResult {
    height: 100%;
    max-height: 500px;
    overflow: auto;
}
.boxSearch .resultSearch .boxResult .listResult li {
    cursor: pointer;
    padding: 10px  20px;
}
.boxSearch .resultSearch .boxResult .listResult li:hover {
    background: #1A3442;
}
.boxSearch .resultSearch .boxResult .listResult li a {
    text-decoration: none;
}
.boxSearch .resultSearch .boxResult .listResult li .titleItem {
    font-size: 13px;
    color: #868F93;
    white-space: nowrap;
    width: 100%;
    overflow: hidden;
    text-overflow: ellipsis;
    display: block;
}
.boxSearch .resultSearch .boxResult .listResult li .description {
    font-size: 14px;
    color: #fff;
    white-space: nowrap;
    width: 100%;
    overflow: hidden;
    text-overflow: ellipsis;
    display: block;
}
.boxSearch .resultSearch .boxResult .listResult li .notfound {
    font-size: 14px;
    color: white;
    text-align: center;
    display: block;
}
.searchMobile {
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background: rgba(0, 0, 0, 0.5);
    z-index: 9999;
}
.searchMobile .boxSearch {
    height: 100%;
    width: 100%;
    padding: 50px 20px;
    background: none;
}
.searchMobile .boxSearch .search {
    background: #142C37;
    padding: 5px 5px;
    border-radius: 5px;
}
.searchMobile .boxSearch .search #iconSearch {
    left: 10px;
    top: 10px;
    width: 20px;
    height: 20px;
    background-size: contain;
    background-image: url(data:image/svg+xml;base64,);
}
.searchMobile .boxSearch .resultSearch {
    top: 100px;
    left: 20px;
    width: calc(100% - 40px);
    max-height: calc(100% - 150px);
    height: auto;
    overflow-y: auto;
    z-index: 10;
    border-radius: 10px;
}
.searchMobile .boxSearch .resultSearch .boxResult {
    height: 100%;
    overflow: hidden;
}
.searchMobile .boxSearch .resultSearch .boxResult .listResult {
    max-height: 100% !important;
}
.keyboard {
    position: fixed;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 9999;
    font-size: .48rem;
    line-height: .48rem;
    background: #f6f6f6;
    color: #4a4a4a;
    width: 100%;
    height: 4.4rem;
    user-select: none;
    visibility: visible;
    transform: translate3d(0, 0, 0);
    transition-property: visibility;
    transition-duration: .2s;
    font-weight: normal;
    border-top: 1px solid #e5e5e5;
}
.keyboard.hidden {
    visibility: hidden;
    transform: translate3d(0, 100%, 0);
}
.keyboard table {
    width: 100%;
    height: 100%;
    border-collapse: collapse;
    table-layout: fixed;
}
.keyboard td {
    text-align: center;
    width: 25%;
    border: 1px solid #ddd;
}
.keyboard td:nth-of-type(-n+3) {
    font-weight: normal;
}
.keyboard td:active {
    background-color: #ddd;
}
.keyboard td:first-child {
    border-left: none;
}
.keyboard td:last-child {
    border-right: none;
}
.keyboard td.active {
    background: #dff0ff;
}
.keyboard td.transition {
    transition: background ease .1s;
}
.keyboard .icon {
    font-size: .34rem;
    background-size: contain;
    background-position: center;
    background-repeat: no-repeat;
    width: 1.5em;
    height: 1.5em;
    display: inline-block;
}
.keyboard .icon svg {
    width: 1.5em;
    height: 1.5em;
}
.keyboard .icon.del {
    background-image: url(data:image/svg+xml;base64,);
}
.keyboard .icon.clear {
    background-image: url(data:image/svg+xml;base64,);
}
.keyboard .icon.keyfold {
    background-image: url(data:image/svg+xml;base64,);
}
.keyboard td[data-code='K'] {
    font-size: .34rem;
    background: #2b91e2;
    color: #ddd;
}
.keyboard td[data-code='K'].active {
    color: #fff;
}
@keyframes cursor {
    0% {
        opacity: 1;
    }
    50% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}
.keyboard-input {
    display: inline-block;
    text-align: right;
    width: 100%;
    height: 100%;
}
.keyboard-input.keyboard-focus {
    position: relative;
}
.keyboard-input.keyboard-focus::after {
    content: '';
    position: absolute;
    right: 0;
    top: 10%;
    height: 80%;
    border-right: 0.02rem solid #108EE9;
    animation: cursor infinite 1s step-start;
}
.keyboard-input span {
    display: inline-block;
}
.keyboard-input .placeholder {
    color: #ddd;
}

/**
   * css for text-left
   */
.keyboard-input.left {
    text-align: left;
}
.keyboard-input.left .icon-clear {
    position: absolute;
    right: 0;
    top: 50%;
    transform: translateY(-50%);
    padding: .2rem;
    display: flex;
}
.keyboard-input.left .icon-clear svg {
    fill: #8E8E93;
    width: 0.28rem;
    height: 0.28rem;
}
.keyboard-input.left.keyboard-focus::after {
    display: none;
}
.keyboard-input.left.keyboard-focus .input-value {
    position: relative;
}
.keyboard-input.left.keyboard-focus .input-value::before {
    content: ' ';
    display: inline-block;
}
.keyboard-input.left.keyboard-focus .input-value::after {
    content: '';
    position: absolute;
    right: 0;
    top: 10%;
    height: 80%;
    border-right: 0.02rem solid #108EE9;
    animation: cursor infinite 1s step-start;
}
.color-white {
    color: #fff;
}
.table-two-column {
    border-collapse: collapse;
    border-radius: 1em;
    overflow: hidden;
    box-shadow: 1px 1px 5px rgba(0, 0, 0, 0.4);
    text-align: center;
}
.table-two-column th, .table-two-column td {
    padding: 1em;
    border-bottom: 1px solid #EDEDED;
}
.table-two-column tr td:nth-child(2), .table-two-column tr th:nth-child(2) {
    background: #F0972D;
    color: #fff;
}
@media (max-width: 1024px) {
    .faqs ol {
        padding-inline-start: 19px;
    }
    .faqs ol p {
        margin: 2px;
    }
}
#bubbles {
